      jianZhiId: "",
      jii: 9,
    };
  },
  computed: {
    config() {
      return {
        title: "rrrrrrr",
        desc: "描述rrrrr",
        img: require("../assets/quan.png"),
        img_title: "朋友圈",
        link: "当前链接",
      };
    },
  },
  methods: {
    toPerview() {
      window.location.href =
        "/a/common/student/applicantInterview/toInterview?bilateralchosefairOrderId=" +
        this.xuanId;
      // this.getDetailContent();
      this.getll();
    },
    toShar(item) {
      if (item.name == "微博") {
        var url =
          "http://v.t.sina.com.cn/share/share.php" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("招聘在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
      } else if (item.name == "QQ") {
        var url =
          "https://connect.qq.com/widget/shareqq/index.html" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("招聘在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
