                  height.offsetHeight,
                  6
                );
                let height1 = this.$refs.element1;
                console.log(height1.offsetHeight, 63);
                this.isPlay = "none";
                this.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                var temp = this.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                console.log(temp, 4);
                if (temp <= 0) {
                  var jo = "0%";
                } else {
                  this.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                  var jo = this.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                }

                console.log(jo);
                this.tex = "展开剩余" + jo;
                this.jii = jo;
                this.ir = 0;
              }, 0);
